const tooltipTextHandler = (type, isActive) => {
    if(isActive && type !== ""){
      if(type === ' ') return " "
      else if(type === " ") return " "
    }
    else return type;
}

const limitInfo = (text, isLimited) => {
    if(!text) return;
    if(isLimited){ return text.substring(0, 40) + "..."; }
    else{ return text; }
}
  
const getMovieLengthTitle = type => type === "movie"? " ": " "
const checkIfNew = year => +year > 2021? <span className='text-success fw-bolder'></span>: null;

export {
    tooltipTextHandler,
    limitInfo,
    getMovieLengthTitle,
    checkIfNew
}